"use client";

import React, { useState } from "react";
import { FiUser } from "react-icons/fi";
import { useTranslations } from 'next-intl';
import { useRouter } from 'next/navigation';

export default function HomePage() {
  const t = useTranslations();
  const [loggedIn, setLoggedIn] = useState(false);
  const [isRegister, setIsRegister] = useState(false);
  
  // Form state
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    if (username && password) {
      setLoggedIn(true);
    }
  };

  const handleRegister = (e: React.FormEvent) => {
    e.preventDefault();
    if (username && password && email) {
      setLoggedIn(true);
    }
  };

  if (!loggedIn) {
    return (
      <div className="max-w-md mx-auto mt-20 p-6 bg-gray-900 rounded-lg shadow-lg">
        <h2 className="text-3xl font-semibold mb-6 text-center">
          {isRegister ? t('auth.register') : t('auth.welcome')}
        </h2>
        <form onSubmit={isRegister ? handleRegister : handleLogin} className="space-y-4">
          {isRegister && (
            <input
              type="email"
              placeholder={t('auth.email')}
              className="w-full p-3 rounded bg-gray-800 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-white"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          )}
          <input
            type="text"
            placeholder={t('auth.username')}
            className="w-full p-3 rounded bg-gray-800 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-white"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder={t('auth.password')}
            className="w-full p-3 rounded bg-gray-800 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-white"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <button
            type="submit"
            className="w-full bg-white text-black font-semibold py-3 rounded hover:bg-gray-200 transition"
          >
            {isRegister ? t('auth.registerButton') : t('auth.loginButton')}
          </button>
        </form>
        <div className="mt-4 text-center">
          <button
            className="text-sm text-gray-400 hover:text-white"
            onClick={() => setIsRegister(!isRegister)}
          >
            {isRegister ? t('auth.alreadyAccount') : t('auth.noAccount')}
          </button>
        </div>
      </div>
    );
  }

  // Dashboard after login
  return (
    <div className="flex h-[80vh] max-w-7xl mx-auto bg-gray-900 rounded-lg shadow-lg overflow-hidden">
      {/* Servers sidebar */}
      <aside className="w-64 bg-gray-800 p-4 flex flex-col">
        <div className="mb-6">
          <h3 className="text-xl font-semibold mb-4">{t('dashboard.servers')}</h3>
          <ul className="space-y-2">
            <li className="p-2 bg-gray-700 rounded cursor-pointer hover:bg-gray-600">Server 1</li>
            <li className="p-2 bg-gray-700 rounded cursor-pointer hover:bg-gray-600">Server 2</li>
            <li className="p-2 bg-gray-700 rounded cursor-pointer hover:bg-gray-600">Server 3</li>
          </ul>
        </div>
        <div className="mt-auto">
          <button className="w-full py-2 bg-gray-700 rounded hover:bg-gray-600 transition">
            {t('dashboard.createServer')}
          </button>
        </div>
      </aside>

      {/* Main chat area */}
      <section className="flex-grow flex flex-col">
        <header className="bg-gray-700 p-4 flex items-center justify-between">
          <h2 className="text-lg font-semibold">#{t('dashboard.general')}</h2>
          <button className="text-white hover:text-gray-300">{t('dashboard.settings')}</button>
        </header>

        <div className="flex-grow p-4 overflow-y-auto bg-gray-900">
          <p className="text-gray-400 italic">{t('dashboard.noMessages')}</p>
        </div>

        <form className="bg-gray-800 p-4 flex items-center space-x-4">
          <input
            type="text"
            placeholder={t('dashboard.typeMessage')}
            className="flex-grow p-3 rounded bg-gray-700 border border-gray-600 focus:outline-none focus:ring-2 focus:ring-white"
          />
          <button
            type="submit"
            className="bg-white text-black px-4 py-2 rounded hover:bg-gray-200 transition"
          >
            {t('dashboard.send')}
          </button>
        </form>
      </section>

      {/* Friends list */}
      <aside className="w-64 bg-gray-800 p-4 flex flex-col">
        <h3 className="text-xl font-semibold mb-4">{t('dashboard.friends')}</h3>
        <ul className="space-y-2 flex-grow overflow-y-auto">
          <li className="p-2 bg-gray-700 rounded cursor-pointer hover:bg-gray-600 flex items-center space-x-2">
            <FiUser />
            <span>Friend 1</span>
          </li>
          <li className="p-2 bg-gray-700 rounded cursor-pointer hover:bg-gray-600 flex items-center space-x-2">
            <FiUser />
            <span>Friend 2</span>
          </li>
          <li className="p-2 bg-gray-700 rounded cursor-pointer hover:bg-gray-600 flex items-center space-x-2">
            <FiUser />
            <span>Friend 3</span>
          </li>
        </ul>
        <button className="mt-4 py-2 bg-gray-700 rounded hover:bg-gray-600 transition">
          {t('dashboard.addFriend')}
        </button>
      </aside>
    </div>
  );
}
